<template>
  <div class="meeting_list h-full w-full p-5">
    <!--<load-view :loading="loading" :progress-data="loadingData" :color="color"></load-view>-->
    <div class="flex-col space-y-10 rounded-xl bg-white p-10 shadow-6">
      <!---->
      <div class="title-before mb-5 flex border-b border-b-gray-300 pb-5">
        <div class="flex flex-1 space-x-2">
          <a-button type="primary" @click="add"></a-button>
          <a-button type="primary" status="success"></a-button>
        </div>
        <div class="inline-block flex items-center space-x-4">
          <div class="flex-1 text-right"></div>
          <el-input v-model="wholeSearch" placeholder="" class="flex-1"></el-input>
        </div>
      </div>
      <!---->
      <el-table :data="tables.slice((currentPage - 1) * pageSize, currentPage * pageSize)" ref="multipleTable" style="width: 100%">
        <el-table-column label="" prop="type" sortable>
          <el-table-column prop="type" width="150">
            <template #header>
              <el-select v-model="search.type" size="mini" placeholder="" clearable>
                <el-option v-for="item in typeSelect" :key="item.value" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </template>
            <template #default="scope">
              <span v-html="showDate(scope.row.type)"></span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" prop="name" sortable>
          <el-table-column prop="name" min-width="250">
            <template #header>
              <el-input placeholder="" v-model="search.name"></el-input>
            </template>
            <template #default="scope">
              <span v-html="showDate(scope.row.name)"></span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" prop="theme" sortable>
          <el-table-column prop="theme" width="200">
            <template #header>
              <el-input placeholder="" v-model="search.theme"></el-input>
            </template>
            <template #default="scope">
              <span v-html="showDate(scope.row.theme)"></span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" prop="date" align="center" sortable>
          <el-table-column prop="date" align="center" width="120">
            <template #header>
              <a-trigger trigger="click" :unmount-on-close="false">
                <el-button class="w-full"></el-button>
                <template #content>
                  <div class="demo-basic rounded-md border border-gray-200 bg-white p-2 shadow-xl">
                    <el-date-picker v-model="search.start" type="date" placeholder="" value-format="YYYY-MM-DD"></el-date-picker>
                    <el-date-picker v-model="search.end" type="date" placeholder="" value-format="YYYY-MM-DD"></el-date-picker>
                  </div>
                </template>
              </a-trigger>
            </template>
            <template #default="scope">
              <span v-html="showDate(scope.row.date)"></span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" prop="principal" align="center" sortable>
          <el-table-column prop="principal" align="center" width="120">
            <template #header>
              <el-input placeholder="" v-model="search.principalName"></el-input>
            </template>
            <template #default="scope">
              <span v-html="showDate(scope.row.principal.name)"></span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" prop="chairman" align="center" sortable>
          <el-table-column prop="chairman" align="center" width="100">
            <template #header>
              <el-input placeholder="" v-model="search.principalDepartment"></el-input>
            </template>
            <template #default="scope">
              <span v-html="showDate(scope.row.principal.department)"></span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" prop="member" sortable>
          <el-table-column prop="member" width="250">
            <template #header>
              <el-input placeholder="" v-model="search.member"></el-input>
            </template>
            <template #default="scope">
              <span v-html="showDate(scope.row.member)"></span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" prop="approvals" align="center" sortable>
          <el-table-column prop="approvals" align="center" width="150">
            <template #header>
              <el-select v-model="search.approvals" size="mini" placeholder="" clearable>
                <el-option v-for="item in approvalsSelect" :key="item.value" :label="item.value" :value="item.value"></el-option>
              </el-select>
            </template>
            <template #default="scope">
              <a-tag v-if="scope.row.approvals === ''" color="arcoblue" v-html="showDate(scope.row.approvals)"></a-tag>
              <div v-else-if="scope.row.approvals === ''" class="flex items-center space-x-2">
                <a-tag v-show="scope.row.approvals === ''" color="red" v-html="showDate(scope.row.approvals)"></a-tag>
                <a-button v-show="scope.row.approvals === '' && scope.row.principal.name === ''" type="primary" @click="sendPetition(scope.row)"></a-button>
              </div>
              <a-tag v-else color="purple" v-html="showDate(scope.row.approvals)"></a-tag>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" prop="remark" sortable>
          <el-table-column prop="remark" width="250">
            <template #header>
              <el-input placeholder="" v-model="search.remark"></el-input>
            </template>
            <template #default="scope">
              <span v-if="scope.row.remark === ''"></span>
              <span v-else v-html="showDate(scope.row.remark)"></span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="" align="center" fixed="right" width="100">
          <template #header>
            <div class="w-full text-center"></div>
          </template>
          <template #default="scope">
            <a-button v-if="scope.row.approvals === ''" type="text" class="ml-0 w-full" @click="Edit_PricingList(scope.$index, scope.row)">
              
              <i class="ri-pencil-fill"></i>
            </a-button>
            <a-button v-else type="text" status="success" class="ml-0 w-full" @click="View_PricingList(scope.$index, scope.row)">
              
              <i class="ri-eye-fill"></i>
            </a-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 20, 40]"
        :page-size="pageSize"
        layout="total, sizes,prev, pager, next"
        :total="dataList.length"
        prev-text=""
        next-text=""
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "HomeView",
  components: {},
  created() {
    this.getApi();
  },
  data() {
    return {
      wholeSearch: "",
      currentPage: 1, //1
      pageSize: 10, //
      keywordSearch: "",
      loading: true,
      loadingData: 0,
      color: "#673ab7",

      dateSearch: "",
      typeSelect: [
        {
          text: "",
          value: "",
        },
        {
          text: "",
          value: "",
        },
        {
          text: "",
          value: "",
        },
        {
          text: "",
          value: "",
        },
      ], //
      approvalsSelect: [
        {
          value: "", //
        },
        {
          value: "", //
        },
        {
          value: "",
        },
      ], //
      dataList: [],
      /**  */
      search: {
        type: "",
        name: "",
        theme: "",
        principalName: "",
        principalDepartment: "",
        chairman: "",
        member: "",
        approvals: "",
        remark: "",
        start: "",
        end: "",
      },
    };
  },
  methods: {
    // API
    getApi() {
      function getAPI() {
        // return axios.get("http://localhost:3000/meetingList");
        return axios.get("https://krytabo.github.io/meetings_vue3/db.json");
      }

      Promise.all([getAPI()]).then((response) => {
        // this.dataList = response[0].data;
        this.dataList = response[0].data.meetingList;
        this.loadingData = 100;
      });

      setTimeout(() => {
        if (this.loadingData === 100) {
          this.loading = false;
        }
      }, 1500);
    },

    Edit_PricingList(index, result) {
      let data = JSON.stringify(result);
      this.$router.push({ path: "editMeeting", query: { res: data, editStatus: true } });
      /*this.$router.push({
        path: "editMeeting",
        query: {
          id: row.id,
          principal: row.principal,
          type: row.type,
          name: row.name,
          textarea: row.textarea,
          chairman: row.chairman,
          record: row.record,
          date: row.date,
          time: row.time,
          theme: row.theme,
          member: row.member,
          offer: row.offer,
          accessory: row.accessory,
          fileList: row.fileList,
          detailList: JSON.parse(JSON.stringify(row.detailList)),
        },
      });*/
    },

    View_PricingList(index, result) {
      let data = JSON.stringify(result);
      this.$router.push({ path: "editMeeting", query: { res: data, editStatus: false } });
    },

    add() {
      this.$router.push({ name: "addMeeting" });
    },

    getTime(val) {
      //   
      var now = new Date();
      val === 0 ? "" : now.setTime(now.getTime() - 24 * 60 * 60 * 1000 * val); //   val
      // val === 0 ? "" : now.setTime(now.getTime() + 24 * 60 * 60 * 1000 * val); //   val
      const hh = (now.getHours() + "").padStart(2, "0");
      const mm = (now.getMinutes() + "").padStart(2, "0");
      const ss = (now.getSeconds() + "").padStart(2, "0");
      //
      let wk = now.getDay();
      let weeks = ["", "", "", "", "", "", ""];
      let week = weeks[wk];
      this.form.time = week;
      return `${hh}:${mm}:${ss}`; //  2021-11-16 11:37:15
      //return `${y}${m}${d} ${hh}:${mm}:${ss}`; //  20211116 11:37:15
      // return `${y}-${m}-${d} ${hh}:${mm}:${ss}`; //  2021-04-09 11:37:15
      // return y + m + d                           //   20210409
    },

    // 
    sendPetition(index, row) {
      console.log(row);
      this.$swal.fire({
        title: "",
        text: "",
        icon: "warning",
        showCancelButton: true,
        cancelButtonColor: "#0084ff",
        confirmButtonColor: "#cccccc",
        confirmButtonText: "",
        cancelButtonText: "",
        closeOnConfirm: false,
      });
    },

    // 
    showDate(val) {
      val = val + "";
      if (val.indexOf(this.wholeSearch) !== -1 && this.wholeSearch !== "") {
        return val.replace(this.wholeSearch, '<span style="color: #000; background: gold">' + this.wholeSearch + "</span>");
      } else {
        return val;
      }
    },

    // table
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },

    //
    handleSizeChange: function (size) {
      this.pageSize = size;
    },

    //
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
    },

    // 
    handleDelete(index, row) {
      this.configForm = row;
      const id = this.configForm.id;

      this.$swal
        .fire({
          title: "",
          text: "",
          icon: "warning",
          showCancelButton: true,
          cancelButtonColor: "#0084ff",
          confirmButtonColor: "#cccccc",
          confirmButtonText: "",
          cancelButtonText: "",
          closeOnConfirm: false,
        })
        .then((result) => {
          if (result.isConfirmed) {
            this.axios
              .delete("http://localhost:3000/configForm/" + `${id}`, row)
              .then((response) => {
                this.configForm.id = response.data.id;
                this.getApi();
              })
              .catch((error) => {
                console.error("There was an error!", error);
              });
          }
        })
        .catch(() => {});
    },

    //
    localizeDate(date) {
      if (!date || !date.includes("-")) return date;
      const [yyyy, mm, dd] = date.split("-");
      return new Date(`${mm}/${dd}/${yyyy}`);
    },
  },
  computed: {
    // 
    /*tables() {
      const search = this.wholeSearch;
      if (search) {
        return this.dataList.filter((data) => {
          return Object.keys(data).some((key) => {
            return String(data[key]).toLowerCase().indexOf(search) > -1;
          });
        });
      }
      return this.dataList;
    },*/

    tables() {
      const search = this.wholeSearch;
      const search2 = this.search.type;
      const search3 = this.search.name;
      const search4 = this.search.theme;
      const search5 = this.search.principalName;
      const search6 = this.search.principalDepartment;
      const search7 = this.search.member;
      const search8 = this.search.approvals;
      const search10 = this.search.remark;

      if (search) {
        return this.dataList.filter((data) => {
          return Object.keys(data).some((key) => {
            // return String(data[key]).toLowerCase().indexOf(search) > -1;
            return String(data[key]).toLowerCase().includes(search.toLowerCase());
          });
        });
      } else if (search2) {
        return this.dataList.filter((data) => {
          return String(data.type).toLowerCase().includes(search2.toLowerCase());
        });
      } else if (search3) {
        return this.dataList.filter((data) => {
          return String(data.name).toLowerCase().includes(search3.toLowerCase());
        });
      } else if (search4) {
        return this.dataList.filter((data) => {
          return String(data.theme).toLowerCase().includes(search4.toLowerCase());
        });
      } else if (search5) {
        return this.dataList.filter((data) => {
          return String(data.principal.name).toLowerCase().includes(search5.toLowerCase());
        });
      } else if (search6) {
        return this.dataList.filter((data) => {
          return String(data.principal.department).toLowerCase().includes(search6.toLowerCase());
        });
      } else if (search7) {
        return this.dataList.filter((data) => {
          return String(data.member).toLowerCase().includes(search7.toLowerCase());
        });
      } else if (search8) {
        return this.dataList.filter((data) => {
          return String(data.approvals).toLowerCase().includes(search8.toLowerCase());
        });
      } else if (search10) {
        return this.dataList.filter((data) => {
          return String(data.remark).toLowerCase().includes(search10.toLowerCase());
        });
      } else {
        const startDone = this.localizeDate(this.search.start);
        const endDone = this.localizeDate(this.search.end);
        return this.dataList.filter((item) => {
          const itemDate = new Date(item.date);
          if (startDone && endDone) {
            return startDone <= itemDate && itemDate <= endDone;
          }
          if (startDone && !endDone) {
            return startDone <= itemDate;
          }
          if (!startDone && endDone) {
            return itemDate <= endDone;
          }
          return true;
        });
      }

      // return this.dataList;
    },
  },
};
</script>
