      const d = (now.getDate() + "").padStart(2, "0");
      let wk = now.getDay();
      let weeks = ["", "", "", "", "", "", ""];
      let week = weeks[wk];
      this.nowWeek = week;
      return `${y}-${m}-${d}`;
