    /** Chckbox */
    handleChecked(val, item) {
      if (val) {
        this.form.accessory.push(item);
      } else {
        this.deleteItem(item.id, this.form.accessory);
      }
    },
    deleteItem(item, list) {
      for (var key in list) {
        if (list[key].id === item) {
          list.splice(key, 1);
        }
      }
    },
    /**  */
    beforeRemove(file) {
      return this.$confirm(` ${file.name}`);
    },
